.clipboard > p {
  margin-bottom: 4.375rem;
}
@media (min-width: 71.875em) {
  .clipboard > p {
    font-size: 1.125rem;
    margin-bottom: 6.5625rem;
  }
}
.clipboard__img {
  position: relative;
  background-image: url("../images/image-devices.png");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
  height: 13.75rem;
}
@media (min-width: 71.875em) {
  .clipboard__img {
    background: none;
    height: 29.5rem;
  }
  .clipboard__img::after {
    content: "";
    background-image: url("../images/image-devices.png");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    position: absolute;
    top: -5.75rem;
    right: -6.25rem;
    bottom: -5.9375rem;
    left: -6.25rem;
  }
}